.App {
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.messages {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.input-wrapper {
  display: flex;
}

.input-message {
  width: 100%;
}

.chat-wrapper {
  width: 500px;
  background-color: antiquewhite;
  height: auto;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.chatbox {
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: left;
}

.chatbox.current {
  align-items: end;
}

.username {
  width: auto;
}
